.social {
  &__list {
    display: flex;
    justify-content: center;
    margin-bottom: 40px;
  }

  &__link {
    background-color: #81b3d2;
    color: #ffffff;
    width: 48px;
    height: 48px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__item {
    list-style: none;
    margin-right: 6px;
    display: flex;

    :hover {
      background-color: #669ec0;
    }

    &__item:nth-child(3) {
      margin-right: 0;
    }
  }
}

@media (min-width: $tablet-width) {
  .social {
    &__list {
      display: flex;
      justify-content: center;
      margin-bottom: 0;
    }

    &__item {
      list-style: none;

      :hover {
        background-color: #669ec0;
      }
    }

    &__link {
      background-color: #81b3d2;
      color: #ffffff;
      width: 48px;
      height: 48px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
}

@media (min-width: $desktop-width) {
  .social {
    &__list {
      display: flex;
      justify-content: center;
      margin-bottom: 0;
    }

    &__item {
      list-style: none;

      :hover {
        background-color: #669ec0;
      }
    }

    &__link {
      background-color: #81b3d2;
      color: #ffffff;
      width: 48px;
      height: 48px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
}
